import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";
import Json from '@/Json';
import axios from '@/utils/uniAxios.js'
export default {
	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			src: '../static/nogoods.png',
			msg: '你还没有任何订单，看看其他的吧',
			tabCurrentIndex: 0,
			navList: [{
					status: '',
					text: '全部',
					loadingType: 'more',
					orderList: []
				},
				{
					status: 0,
					text: '待付款',
					loadingType: 'more',
					orderList: []
				},
				{
					status: 1,
					text: '待发货',
					loadingType: 'more',
					orderList: []
				},
				{
					status: 2,
					text: '待收货',
					loadingType: 'more',
					orderList: []
				},
				{
					status: 3,
					text: '待评价',
					loadingType: 'more',
					orderList: []
				},
			],
			page: {
				current: 1,
				pageSize: 10
			},
			orderList: [],
			payCode: {
				appId: "",
				nonceStr: "",
				package: "",
				paySign: "",
				signType: "MD5",
				timeStamp: ""
			},
			loadingText: '加载中...',
			optionState: '',
			scrollTop: 0,
		};
	},

	onLoad(options) {
		/**
		 * 修复app端点击除全部订单外的按钮进入时不加载数据的问题
		 * 替换onLoad下代码即可
		 */
		console.log(options)
		this.tabCurrentIndex = +options.state;
		this.optionState = options.state
		if (options.state === '') { //全部订单
			this.loadData('');
		} else if (options.state === 0) { //待付款
			this.loadData(options.state);
		} else {
			this.loadData(options.state);
		}
	},
	onReachBottom() {

	},

	methods: {
		btn() {
			uni.switchTab({
				url: '../index/index'
			})
		},
		lower(e) {
			this.page.current++;
			setTimeout(() => {
				this.getmorenews();
			}, 1000)
		},
		scroll(e) {

		},
		getmorenews() {
			console.log(111)
			if (this.loadingText == '已全部加载') {
				return false;
			}
			this.loadingText = '加载中...';
			uni.showNavigationBarLoading();
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			let state = navItem.status; //每个状态的值
			let obj = {
				"orderType": 0, //订单类型
				"pageNum": this.page.current, //页码
				"pageSize": this.page.pageSize, //页数
				"sourceType": 1, //订单来源
				"status": this.optionState //订单状态：0->待付款；1->待发货；2->已发货(待收货)；3->已完成(待评价)；4->已关闭；5->无效订单
			}
			axios.post('/order/list', obj).then(res => {
				this.loadingText = '';
				if (res.data.data.list.length == 0 || navItem.orderList.length == res.data.data.total) {
					this.loadingText = '已全部加载';
					return false
				}

				let result = []
				result = result.filter(item => {
					//添加不同状态下订单的表现形式
					item = Object.assign(item, this.orderStateExp(item.status));
					if (state === '') {
						//0为全部订单
						return item;
					}
					return item.status === state
				});
				this.orderList = this.orderList.concat(result);
				this.orderList.forEach(item => {
					navItem.orderList.push(item);
				})
				this.loadingText = '上拉加载更多';
				uni.hideNavigationBarLoading();
				this.$set(navItem, 'loaded', true);
			})

		},
		//获取订单列表
		loadData(n) {
			//这里是将订单挂载到tab列表下
			this.page.current = 1;

			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			let state = navItem.status; //每个状态的值
			navItem.orderList = [];
			let obj = {
				"orderType": 0, //订单类型
				"pageNum": this.page.current, //页码
				"pageSize": this.page.pageSize, //页数
				"sourceType": 1, //订单来源
				"status": n //订单状态：0->待付款；1->待发货；2->已发货(待收货)；3->已完成(待评价)；4->已关闭；5->无效订单
			}

			axios.post('/order/list', obj).then(res => {
				if (res.data.code == '200') {
					let result = res.data.data.list;
					this.orderList = result.filter(item => {
						//添加不同状态下订单的表现形式
						item = Object.assign(item, this.orderStateExp(item.status));
						if (state === '') {
							//0为全部订单
							return item;
						}
						return item.status === state
					});

					this.orderList.forEach(item => {
						navItem.orderList.push(item);
					})
					console.log(navItem, 777)
					this.$set(navItem, 'loaded', true);
					navItem.loadingType = 'more';
					if (this.orderList.length == res.data.data.total) {
						this.loadingText = '已全部加载'
						return false
					} else {
						this.loadingText = '上拉加载更多'
					}
				}
			})
		},

		//swiper 切换
		changeTab(e) {
			if (e.target) { //在本页面点击tab切换
				this.tabCurrentIndex = e.target.current;
				if (this.tabCurrentIndex == 0) { //全部
					this.optionState = ''
					this.loadData('');
				} else {
					this.optionState = this.tabCurrentIndex - 1;
					this.loadData(this.tabCurrentIndex - 1);
				}
			} else { //从我的页面进入
				this.loadData(this.tabCurrentIndex - 1);
			}
		},
		//顶部tab点击
		tabClick(index) {
			this.tabCurrentIndex = index;
			// if (this.tabCurrentIndex == 0) { //全部
			// 	this.loadData('');
			// } else {
			// 	this.loadData(this.tabCurrentIndex - 1);
			// }
		},
		// 评价
		evaluate(item) {
			console.log(item)
		},
		// 再次购买
		againBuy(item) {
			console.log(item)
			let id = item.productId;
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		},
		//取消订单
		cancelOrder(item) {
			console.log(item,this.navList[1].orderList)
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(() => {
				
				//取消订单后删除待付款中该项	
				let list = this.navList[1].orderList;
				
				let index = list.findIndex(val => val.id === item.id);
				console.log(index)
				list.splice(index, 1);
				console.log(list)
				uni.hideLoading();
				// axios.post('/order/cancelOrder', {
				// 	orderId: item.id
				// }).then(res => {
				// 	if (res.data.code == 200) {
				// 		this.$api.msg('取消成功')
				// 	}
				// })
			}, 600)
		},

		//订单状态文字和颜色
		orderStateExp(state) {
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch (+state) {
				case 0:
					stateTip = '待支付';
					break;
				case 1:
					stateTip = '待发货';
					break;
				case 2:
					stateTip = '待收货';
					break;
				case 3:
					stateTip = '待评价';
					break;
				case 5:
					stateTip = '';
					break;
				case 6:
					stateTip = '交易成功';
					break;
				case 7:
					stateTip = '已完成';
					break;
				case 9:
					stateTip = '订单已关闭';
					stateTipColor = '#909399';
					break;
			}
			return {
				stateTip,
				stateTipColor
			};
		},
		//订单详情
		orderDetails(item, id) {
			console.log(item)
			if (item.status != 3) {
				uni.navigateTo({
					url: 'orderDetails?status=' + item.status + '&id=' + id
				})
			}

		},
		// 物流信息
		logisticsTap(item) {
			uni.navigateTo({
				url: 'logistics?id=' + item.id
			})
		},
		// 立即支付
		payGoods(itemSn, itemAmount) {
			console.log(itemSn, itemAmount)
			let code = uni.getStorageSync('code')
			let obj = {
				code: code, //code
				orderSn: itemSn, //订单编号orderSn
				payType: 3, //支付类型
				rechargeMoney: itemAmount, //支付金额s
			}
			let _this = this;
			axios.post('/pay/payOrder', obj).then(res => {
				_this.payCode = {
					appId: res.data.data.appId,
					nonceStr: res.data.data.nonceStr,
					package: res.data.data.package,
					paySign: res.data.data.paySign,
					signType: "MD5",
					timeStamp: res.data.data.timeStamp,
				}
				uni.requestPayment({
					provider: 'wxpay',
					timeStamp: _this.payCode.timeStamp,
					nonceStr: _this.payCode.nonceStr,
					package: _this.payCode.package,
					signType: 'MD5',
					paySign: _this.payCode.paySign,
					success: function(res) {
						console.log('success:' + JSON.stringify(res));
						uni.reLaunch({
							url: 'paySuccess?totalCount=' + _this.totalCount + '&id=' + _this.orderList.id
						})
					},
					fail: function(err) {
						console.log('fail:' + JSON.stringify(err));
					}
				})
			})
		}
	},
}
